"use client";

import React, { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Checkbox } from "@/components/ui/checkbox";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";

interface TransferEntry {
  code: string;
  quantity: number;
  fromStore: string;
  toStore: string;
  name: string;
  date: string;
  confirmed: boolean;
}

const ADMIN_USER = "admin";
const ADMIN_PASS = "password";

export default function AdminPage() {
  const [user, setUser] = useState("");
  const [pass, setPass] = useState("");
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [transferData, setTransferData] = useState<TransferEntry[]>([]);
  const [confirmations, setConfirmations] = useState<{ [key: string]: boolean }>({});
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    if (error) {
      const timer = setTimeout(() => setError(null), 5000);
      return () => clearTimeout(timer);
    }
    if (success) {
      const timer = setTimeout(() => setSuccess(null), 5000);
      return () => clearTimeout(timer);
    }
  }, [error, success]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await fetch('/api/transfers');
        if (!res.ok) throw new Error('Failed to fetch transfers');
        const data = await res.json();
        setTransferData(data);

        const confRes = await fetch('/api/confirmations');
        if (!confRes.ok) throw new Error('Failed to fetch confirmations');
        const confData = await confRes.json();
        setConfirmations(confData);
      } catch (error) {
        setError("Error loading data: " + (error instanceof Error ? error.message : String(error)));
      }
    };

    if (isAuthenticated) {
      fetchData();
    }
  }, [isAuthenticated]);

  const handleLogin = () => {
    if (user === ADMIN_USER && pass === ADMIN_PASS) {
      setIsAuthenticated(true);
      setSuccess("Login successful");
    } else {
      setError("Invalid username or password");
    }
  };

  const toggleConfirmation = async (key: string) => {
    setIsLoading(true);
    const newConfirmations = { ...confirmations, [key]: !confirmations[key] };
    try {
      const res = await fetch('/api/confirmations', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(newConfirmations),
      });
      if (!res.ok) throw new Error('Failed to update confirmations');
      setConfirmations(newConfirmations);
      setSuccess("Confirmation status updated successfully");
    } catch (error) {
      setError("Error updating confirmations: " + (error instanceof Error ? error.message : String(error)));
      // Revert the change in UI
      setConfirmations((prev) => ({ ...prev }));
    } finally {
      setIsLoading(false);
    }
  };

  if (!isAuthenticated) {
    return (
      <main className="min-h-screen bg-gray-50 flex flex-col items-center justify-center p-6 font-sans">
        <Card className="w-full max-w-sm">
          <CardHeader>
            <CardTitle className="text-2xl text-center">Admin Login</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div>
              <Input
                type="text"
                placeholder="Username"
                value={user}
                onChange={(e) => setUser(e.target.value)}
              />
            </div>
            <div>
              <Input
                type="password"
                placeholder="Password"
                value={pass}
                onChange={(e) => setPass(e.target.value)}
              />
            </div>
            <Button
              onClick={handleLogin}
              className="w-full"
              variant="default"
            >
              Login
            </Button>
          </CardContent>
        </Card>
        {error && (
          <Alert variant="destructive" className="mt-4 w-full max-w-sm">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}
      </main>
    );
  }

  // Group entries by name and date
  const groupedEntries: { [key: string]: TransferEntry[] } = {};
  transferData.forEach((entry) => {
    const key = `${entry.name}-${entry.date}`;
    if (!groupedEntries[key]) {
      groupedEntries[key] = [];
    }
    groupedEntries[key].push(entry);
  });

  return (
    <main className="min-h-screen bg-gray-50 p-6 font-sans">
      <Card>
        <CardHeader>
          <CardTitle className="text-2xl">Admin Panel - Transfer Requests</CardTitle>
        </CardHeader>
        <CardContent>
          {transferData.length === 0 ? (
            <p className="text-gray-500 text-center py-4">No transfer requests found.</p>
          ) : (
            <div className="space-y-6">
              {Object.entries(groupedEntries).map(([key, entries]) => {
                const [name, date] = key.split("-");
                const confirmed = confirmations[key] || false;
                return (
                  <Card key={key}>
                    <CardContent className="pt-6">
                      <div className="flex justify-between items-center mb-4">
                        <div>
                          <p className="font-semibold">Name: {name}</p>
                          <p className="text-sm text-gray-600">
                            Date: {new Date(date).toLocaleString()}
                          </p>
                        </div>
                        <div className="flex items-center space-x-2">
                          <Checkbox
                            checked={confirmed}
                            onCheckedChange={() => toggleConfirmation(key)}
                            disabled={isLoading}
                          />
                          <span>Confirmed</span>
                        </div>
                      </div>
                      <Table>
                        <TableHeader>
                          <TableRow>
                            <TableHead>Code</TableHead>
                            <TableHead>Quantity</TableHead>
                            <TableHead>From Store</TableHead>
                            <TableHead>To Store</TableHead>
                          </TableRow>
                        </TableHeader>
                        <TableBody>
                          {entries.map((entry, idx) => (
                            <TableRow key={idx}>
                              <TableCell>{entry.code}</TableCell>
                              <TableCell>{entry.quantity}</TableCell>
                              <TableCell>{entry.fromStore}</TableCell>
                              <TableCell>{entry.toStore}</TableCell>
                            </TableRow>
                          ))}
                        </TableBody>
                      </Table>
                    </CardContent>
                  </Card>
                );
              })}
            </div>
          )}
        </CardContent>
      </Card>

      {error && (
        <Alert variant="destructive" className="mt-4">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}
      
      {success && (
        <Alert className="mt-4">
          <AlertDescription>{success}</AlertDescription>
        </Alert>
      )}
    </main>
  );
}
